"use client";

import Link from "next/link";
// Import the hook to get the current URL path
import { usePathname } from "next/navigation";
import { ShoppingCart } from "lucide-react";
import { Button } from "@/components/ui/button";
import { useCart } from "@/context/CartContext";

export function Header() {
  const { itemCount } = useCart();
  // Get the current path
  const pathname = usePathname();

  // Helper function to define the common class names
  const baseClasses = "font-medium";
  // Helper function to determine the text color based on the current path
  const getLinkClasses = (href: string) => {
    // If the current pathname matches the link's href, apply the active style
    const isActive = pathname === href;
    return `${baseClasses} ${
      isActive
        ? "text-primary border-b-2 border-primary"
        : "text-foreground hover:text-primary/80"
    }`;
  };

  return (
    <header className="sticky top-0 z-50 w-full border-b border-border bg-background/95 backdrop-blur supports-backdrop-filter:bg-background/60">
      <div className="container mx-auto px-4 flex items-center justify-between gap-4 h-16">
        <div className="flex items-center gap-10">
          {/* Apply the helper function to the Shop link */}
          <Link href="/products" className={getLinkClasses("/")}>
            Shop
          </Link>

          {/* Apply the helper function to the Todo App link */}
          <Link href="/todo" className={getLinkClasses("/todo")}>
            Todo App
          </Link>
        </div>

        <Link href="/cart">
          <Button variant="ghost" size="icon" className="relative">
            <ShoppingCart className="size-5" />
            {itemCount > 0 && (
              <span className="absolute top-1 right-0 w-4 h-4 bg-primary text-primary-foreground rounded-full text-xs font-bold flex items-center justify-center">
                {itemCount}
              </span>
            )}
          </Button>
        </Link>
      </div>
    </header>
  );
}
